@import "../../utilities";

.card {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
  background: $bright-yellow;

  &-visual {
    height: 125px;
    width: 175px;
    float: left;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 0px;
      box-shadow: 2px 0px 10px rgba(0, 0, 0, 0.1);
      filter: drop-shadow(2px 0px 10px rgba(0, 0, 0, 0.1));
    }
  }

  &-content {
    margin-top: 1rem;
    color: $silver;
    font-family: $font-roboto;
    height: auto;
    width: 100%;
    padding: 0px 20px;
    text-align: left;

    h2 {
      font-size: 22px;
      line-height: 29.3px;
      font-weight: 500;
    }

    p {
      font-size: 16px;
      font-weight: normal;
      padding: 10px 0px;
    }

    button {
      font-size: 16px;
      font-weight: normal;
      background: $silver;
      border: $silver;
      outline: none;
      color: $black;
      display: block;
      width: 100%;
      padding: 10px;

      &:hover {
      }
    }
  }
}
