@import "../../utilities";

/* In my case, I use these CSS styles in my App.css file*/
#portfolio {
  height: 80vh;
  background: $black;
  color: $silver;
  width: 100%;
  padding: 0rem 25px 2rem;
  overflow-x: hidden;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  // background: blue;

  .portfolio-content {
    width: 100%;
    text-align: center;

    h1 {
      font-size: 45px;
      line-height: 45px;
      // TODO put paint background
    }

    p {
      padding-top: 20px;
      display: block;
      text-align: center;
      width: 100%;
      font-size: 16px;
      line-height: 21px;
      letter-spacing: 1.1px;
      font-family: $font-roboto;
    }
  }

  .portfolio {
    // padding-bottom: 15px;
    text-align: center;

    h2 {
      font-size: 16px;
      font-weight: bold;
      padding-bottom: 15px;
    }
  }
}

.swiper {
  width: 250px;
  height: 350px;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: bold;
  color: #fff;
}
